.main__user_profile__albums_block {
  display: grid;
  grid-template-rows: 80% 1fr;
  height: 100%;
}

button.albums_block__button {
  background-color: rgb(54, 54, 87);
  color: rgb(158, 158, 158);
  font-size: 20px;
  display: grid;
  justify-self: center;
  align-self: center;
  position: relative;
  width: 50%;
  height: 50%;
}

button.albums_block__button p {
  margin: 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: grid;
  align-items: center;
}

.albums_block__button input {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  cursor: pointer;
}

@media (max-width: 1280px) {
  .main__user_profile__albums_block {
    max-width: 100vw;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
}

@media (max-width: 640px) {
  .main__user_profile__albums_block {
    display: none;
  }
}
