.photoAlbum__photoCard_block__card {
  margin: 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: grid;
  grid-template-rows: 80% 1fr;
}

.photoAlbum__photoCard_block__card__photo {
  max-width: 75%;
  min-width: 100%;
  max-height: 75%;
  min-height: 100%;
}

.photoAlbum__photoCard_block__card__actions {
  display: grid;
  justify-content: center;
}
